.btn {
  padding: 1rem;
  color: #f8f8f8;
  font-weight: 700;
  font-size: 1.5rem;
}

.btnRounded {
  border-radius: 50%;
  height: 100px;
  width: 100px;
}

@media (min-width: 576px) {
  .btnRounded {
    height: 150px;
    width: 150px;
  }
}

@media (min-width: 900px) {
  .btnRounded {
    height: 250px;
    width: 250px;
  }
}

.btn:disabled {
  background-color: #ccc;
}

.resetContainer {
  min-height: 150px;
}
